/** Resources slider */

(function () {
	"use strict";

	// init news and events carousel
	new Swiper(".resources-slider-container", {
		slidesPerView: "auto",
		navigation: false,
		pagination: {
			el: '.resources .swiper-pagination',
			type: 'bullets',
		},
		centeredSlides: false,
		spaceBetween: 30,
		disableOnInteraction: false,
		pauseOnMouseEnter: true
	});

}());
